import React, { useCallback, useState } from 'react'
import { LoginSocialFacebook} from 'reactjs-social-login';
import { FacebookLoginButton} from 'react-social-login-buttons'
import { Link } from "react-router-dom";

export default function Login() {

 

  const [flag, setFlag] = useState(false);
  const [pp, setPp] = useState();
  const [provider, setProvider] = useState('')
  const [profile, setProfile] = useState()

  const responseFacebook = (response) => {
    setFlag(true);
    setPp(response);
    console.log(response);
    console.log(pp);
  };

  const onLoginStart = useCallback(() => {
    alert('login start')
  }, [])
 

  const rendertem = () => {
    const shlink = `http://localhost:3000/Edit/${pp.id}`; // Define shlink here

    return (
      <>
        <div className="navbar">
          <Link className="nav" to={shlink}>
            {shlink}
          </Link>
          <Link className="nav-a">
            <img src={pp.picture.data.url} alt="User Profile" />
          </Link>
        </div>
        <div className="msg-er">
          <h1>Under Development</h1>
          <p>Ongoing Database Development</p>
        </div>
      </>
    );
  };

  return (
    <>
      {flag ? (
        rendertem()
      ) : (
       
        <LoginSocialFacebook
        appId={process.env.REACT_APP_FB_APP_ID || '840655871102987'}
        onLoginStart={onLoginStart}
        fields="name,email,picture" 
        onResolve={({ provider, data }: IResolveParams) => {
          setProvider(provider)
          responseFacebook(data)
          setProfile(data)
          
        }}
        onReject={(err) => {
          console.log(err)
        }}
        
      ><FacebookLoginButton /></LoginSocialFacebook>
      )};

</>
  );
}
